<template>
<section>
    <link rel="stylesheet"
            href="https://cdnjs.cloudflare.com/ajax/libs/twitter-bootstrap/4.5.2/css/bootstrap.min.css" />
        <link rel="stylesheet" href="https://talygenv.github.io/Vue-UI-Content/css/custom.css" />
        <link rel="stylesheet" href="https://talygenv.github.io/Vue-UI-Content/css/tree.css" />
        <link rel="stylesheet" href="https://talygenv.github.io/Vue-UI-Content/css/DropZone.css" />
        <link rel="stylesheet" href="https://talygenv.github.io/Vue-UI-Content/css/custom_cli.css" />
        <link rel="stylesheet" href="https://talygenv.github.io/Vue-UI-Content/css/default.css" />
 
  <div class="loginbg p-0">
    <!-- Default background/message -->

<loader :is-visible="isLoading"></loader>

 <div class="wrapper">
           <div class="jumbotron vertical-center">
                 <div class="login-container">

                    <div class="row">
                      <div class="col-md-12 text-center py-1"><img src="https://talygenv.github.io/Vue-UI-Content/AppLogin/Content/images/DefaultImages/new_loginLogo.png" class="talygen-logo"></div>
                   
                   <div class="cont_login row m-0 loginform col-12 p-0">
    


<div class="col-sm-12  col-md-6  col-lg-6  col-xl-6 talygen-app-store py-4" id="login_downloads">
    <div class="form-group text-center mt-4">
        <h5>Mobile Apps</h5>
        <div class="app-links">
            <a href="https://itunes.apple.com/us/app/talygen-business-intelligence/id606916466?ls=1&amp;mt=8" target="_blank"><i class="fa fa-apple"></i></a>
            <a href="https://play.google.com/store/apps/details?id=com.talygenandroidi3.development&amp;feature=nav_result#?t=W251bGwsMSwyLDNd" target="_blank" class="android"><i class="fa fa-android"></i></a>
        </div>
    </div>
    <div class="form-group text-center mt-4 py-2">
        <h5>Desktop Apps</h5>
        <div class="app-links">
            <a href="https://talygen.com/LinuxApp" target="_blank" class="linux"><i class="fa fa-linux"></i></a>
            <a href="https://talygen.com/WindowsApp" target="_blank" class="window"><i class="fa fa-windows"></i></a>
            <a href="https://talygen.com/MacApp" target="_blank"><img src="https://talygenv.github.io/Vue-UI-Content/AppLogin/Content/Responsive/images/mac.png"></a>
        </div>
    </div>
    <div class="form-group text-center mt-4">
        <h5>Support</h5>
        <div class="app-links">
            <a href="https://forums.talygen.com/" target="_blank" class="user"><i class="fa fa-user"></i></a>
            <a onclick="psmKimow(); return false;" href="javascript:;" class="chat"><i class="fa fa-comment"></i></a>
            <a href="https://talygen.com/support" target="_blank" class="info"><i class="fa fa-info-circle"></i></a>
            <a href="https://talygen.com/versions.htm" target="_blank"><i class="fa fa-clipboard"></i></a>
        </div>
    </div>
</div>
<div id="cimKim" style="z-index: 100; position: fixed;">
</div>
<div class="content-box col-sm-12 col-md-6 col-lg-6 col-xl-6 login-form py-4 px-2" id="tabs">

        <div class="form-group text-center userimg">
            <img alt="" src="https://talygenv.github.io/Vue-UI-Content/AppLogin/Content/images/no_user_image.png">
        </div>



        <div class="content-box-content text-center" id="tabs1">
            <p class="text-white">
               {{ message }}
            </p>
        </div>
       </div>
       </div>
    </div>
   </div>
</div>
</div>
 <!--<div class="text-center">
      <p>{{ message }}</p>
    </div>-->

      <div class="my-popups" v-if="showModal">
            <div class="modal d-block ">
                <div class="modal-dialog modal-dialog-centered ui-draggable" style="max-width:850px">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle py-1 ">
                            <h4 name="header" class="modal-title mt-2 pt-2 mb-2">
                                {{ $t('GenerateOrResetPasswordHeading') }}
                            </h4>
                            <button class="close" v-on:click="ClosePopUp"><em aria-hidden="true"
                                    class="fa fa-times"></em>
                            </button>
                            
                        </div>
                        <div class="listing py-3 px-3">
                            
                            <dynamic-form lang="en" ref="refResetPassword" :buttons="buttons"
                                :schema="FormSchema"></dynamic-form>
                        </div>

                    </div>
                </div>
            </div>
        </div>

    <!-- âœ… Popup Modal -->
    

    <!-- Optional: Modal Backdrop -->
  </div>
   </section>
</template>

<script>
import DataServices from "../../services/DataService";
export default {
    data() {
        return {
            message:'',
            email:'',
            isClientInActive:'',
            isReset:'',
            UserId :0,
            Token : "",
            CompanyId :0,
            DecryptedUserId:0,
            DecryptedToken:"",
            
            showModal: false,
         FormSchema: [
                {
                    layoutType: "triple",
                      //layoutType: "two",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Email'),
                            name: "Email",
                            value: "",
                            placeholder: "Enter Title",
                            visibility: true,
                            config: {
                                options: [],
                                showAddIcon: false,
                            },
                            disabled: true,
                            validationRules: "",
                        },
                        {
                            astype: "PasswordField",
                            label: this.$t('NewPassword'),
                            name: "NewPassword",
                            value: "",
                            placeholder: "Enter New Password",
                            visibility: true,
                            config: {
                                options: [],
                                showAddIcon: false,
                            },
                            validationRules: {
                                'required' : true,
                                'min':6,
                                'regex':/^(?=.*[a-zA-Z])(?=.*\d)(?=.*[!@#$%^&*()_+{}\[\]:;"'<>,.?/\\|`~]).*$/
                            },
                        },
                        {
                            astype: "PasswordField",
                            label: this.$t('ConfirmPassword'),
                            name: "ConfirmPassword",
                            value: "",
                            placeholder: "Enter Confirm Password",
                            visibility: true,
                            config: {
                                options: [],
                                showAddIcon: false,
                            },
                            validationRules: {
                                'required' : true,
                                'min':6,
                                'regex':/^(?=.*[a-zA-Z])(?=.*\d)(?=.*[!@#$%^&*()_+{}\[\]:;"'<>,.?/\\|`~]).*$/
                            },
                        }
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i>Reset Password",
                    onClick: this.onSubmit
                },
                
            ],
            isLoading: false,
        }
        
    },
    created: async function () {
        //debugger;
        var vm = this;
        this.UserId  = this.$route.query.userid;
       this.Token = this.$route.query.token;
       this.CompanyId  = this.$route.query.companyId;
       this.GetActivateClientDetail();
       
    },
    methods: {
       ClosePopUp() {
        //debugger;
        this.showModal = false;
  },
        GetActivateClientDetail ()
         {
            //debugger;
            var vm = this;
            vm.isLoading = true;
            let Url = `userid=${this.UserId}&token=${this.Token}&companyId=${ this.CompanyId}`;
             DataServices.ActivateClient(Url).then(function (response) {
                //debugger;
                if(response.data.success==true){
                    vm.isLoading = false;
                    vm.message = response.data.message;
                    vm.email = response.data.email;
                    vm.isClientInActive = response.data.isClientInActive;
                    vm.isReset = response.data.isReset;
                    vm.FormSchema[0].Data[0].value = vm.email;
                    vm.showModal = response.data.showResetPopup;
                    vm.DecryptedUserId=response.data.userId;
                    vm.DecryptedToken=response.data.decryptedToken
                   
                }
            });
        },

        async onSubmit(event, data) {
       
            var vm = this;
            vm.$refs.refResetPassword.validateForm.validate().then(async function (valid) {
                if(valid){
                    const newPassword = vm.FormSchema[0].Data[1].value;
                    const confirmPassword = vm.FormSchema[0].Data[2].value;

                    // const newPassword = event.NewPassword;
                    // const confirmPassword = event.ConfirmPassword;
                    //const specialChars = /[!@#$%^&*(),.?":{}|<>]/

                    if (newPassword !== confirmPassword) {
                
                        //alert(this.$t('Passwords do not match!'));
                        vm.ShowAlert(vm.$t("Passwords do not match Please enter the correct password!"), "danger", vm.$t('Alert'));

                        return;
                    }
                    else if (newPassword == confirmPassword) {
                    
                        //alert(this.$t('Passwords  match!'));
                        //return;
                    }
                    vm.isLoading = true;
                    try {
                        //const url = `UserId=${vm.UserResetPasswordData.user_id}&Email=${vm.UserResetPasswordData.email_id}&Token=${vm.token}&NewPassword=${newPassword}&ConfirmPassword=${confirmPassword}`;
                        let AdminResetPasswordModel = {
                            UserId: vm.DecryptedUserId,
                            Email: vm.email,
                            Token: vm.DecryptedToken,
                            NewPassword: newPassword,
                            ConfirmPassword: confirmPassword,
                            IsClient:true,
                        }
                        
                        //const response = await DataService.ResetPasswordByAdmin(url);
                        await DataServices.ResetPasswordByAdmin(AdminResetPasswordModel).then(response => {
                            if (response.data.status === 'Success') {
                                vm.ShowAlert(vm.$t("Your password has been reset successfully. Please login with your new password"), "success", vm.$t('Alert'));

                            }
                            console.log(response);
                            vm.isLoading = false;
                            vm.ClosePopUp();
                        })

                    } catch (error) {
                        //console.error(error);
                        vm.isLoading = false;
                    }
                }
            })
            


        },
        

    }
}
</script>